import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { IonicModule } from '@ionic/angular';

import { StardashboardPageRoutingModule } from './stardashboard-routing.module';

import { StardashboardPage } from './stardashboard.page';
import { NgCircleProgressModule } from 'ng-circle-progress';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    StardashboardPageRoutingModule,
    NgCircleProgressModule.forRoot({
      "outerStrokeLinecap": "butt"
    })
  ],
  declarations: [StardashboardPage]
})
export class StardashboardPageModule {}
